export class LoginUserWizardController extends BaseWizardController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '.wizard'
		} );

		super( options );
	}

	init( options ) {
		//this._super('initialize', options );

		this.title = $.i18n._( 'Sign In Override' );
		this.steps = 1;
		this.current_step = 1;

		this.render();
	}

	render() {
		this.wizard_id = 'LoginUser';

		super.render();

		LoginUserWizardController.type = this.default_data;

		this.initCurrentStep();
	}

	buildCurrentStepUI() {

		var $this = this;
		this.content_div.empty();

		this.stepsWidgetDic[this.current_step] = {};

		switch ( this.current_step ) {
			case 1:
				var label = this.getLabel();
				label.text( $.i18n._( 'Pick one employee then click the green checkmark icon to login' ) );
				this.content_div.append( label );

				Global.loadViewSource( 'LoginUser', 'LoginUserView.css' );

				Global.loadViewSource( 'LoginUser', 'LoginUserViewController.js', function() {

					Global.loadViewSource( 'LoginUser', 'LoginUserView.html', function( result ) {
						var args = {};

						var template = _.template( result );
						$this.content_div.append( template( args ) );

					} );
				} );

				break;

		}
	}

	buildCurrentStepData() {
	}

	onCloseClick() {

		$( this.el ).remove();

		LocalCacheData.current_open_wizard_controllers = LocalCacheData.current_open_wizard_controllers.filter( wizard => wizard.wizard_id !== this.wizard_id );
		LocalCacheData.extra_filter_for_next_open_view = null;
	}

	onDoneClick() {

		if ( login_user_view_controller.getGridSelectIdArray().length != 1 ) {
			TAlertManager.showAlert( $.i18n._( 'Choose one Employee' ) );
			return;
		}

		this.onCloseClick();

		if ( this.call_back ) {
			this.call_back( login_user_view_controller.getGridSelectIdArray()[0] );
		}
	}

}

LoginUserWizardController.type = '';